


















































import { Component, Vue, Prop, Watch } from 'vue-property-decorator';
import {
  BButton,
  BCard,
  BForm,
  BFormCheckbox,
  BFormCheckboxGroup,
  BFormGroup,
  BFormInput,
  BFormSelect
} from 'bootstrap-vue';

@Component({
  components: {
    'b-form': BForm,
    'b-form-group': BFormGroup,
    'b-form-input': BFormInput,
    'b-card': BCard,
    'b-form-select': BFormSelect,
    'b-button': BButton,
    'b-form-checkbox-group': BFormCheckboxGroup,
    'b-form-checkbox': BFormCheckbox,
  }
})
export default class App extends Vue {
  public dataParse: any;
  @Prop() private data!: string;

  public form = {
    email: '',
    name: '',
    food: null,
    checked: []
  };
  public foods = [{ text: 'Select One', value: null }, 'Carrots', 'Beans', 'Tomatoes', 'Corn'];
  public show = true;

  public onClick(event: any) {
    event.target.dispatchEvent(new CustomEvent('my-click', {
      bubbles: true,
      detail: 'clicked',
      composed: true,
    }));
  }

  onSubmit(evt: any) {
    evt.preventDefault();
    alert(JSON.stringify(this.form));
  }

  onReset(evt: any) {
    evt.preventDefault();
    // Reset our form values
    this.form.email = '';
    this.form.name = '';
    this.form.food = null;
    this.form.checked = [];
    // Trick to reset/clear native browser form validation state
    this.show = false;
    this.$nextTick(() => {
      this.show = true
    })
  }



  @Watch('data', { immediate: true })
  private onDataChanged(newValue: any) {
    try {
      this.dataParse = JSON.parse(newValue);
    } catch (e) {
      this.dataParse = {};
    }
  }

}
